/* Общие стили для страницы */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #121212; /* Темный фон для всей страницы */
  color: #e0e0e0; /* Светлый текст по умолчанию */
}

.container {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

/* Контейнер для кнопок меню */
.menu-button-container {
  display: flex;
  justify-content: space-around;
  background-color: #1e1e1e; /* Темный фон меню */
  padding: 20px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.8); /* Более мягкая тень */
  border-radius: 20px 20px 0 0;
}

/* Стиль для кнопок */
.menu-button {
  background-color: transparent;
  color: #e0e0e0; /* Светлый текст кнопок */
  border: none;
  font-size: 1rem;
  cursor: pointer;
  display: flex;
  flex-direction: column; /* Иконка над текстом */
  align-items: center; /* Центрирование текста и иконки */
  gap: 5px; /* Отступ между иконкой и текстом */
  transition: color 0.3s ease;
}

/* Стиль для иконок в кнопках */
.menu-button .menu-icon {
  font-size: 24px; /* Размер иконки */
  color: #cccccc; /* Цвет иконки */
}

/* Контейнер для контента */
.content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
  background-color: #1a1a1a; /* Темный фон контента */
  padding: 0 20px;
  position: absolute;
  top: 0;
  width: 100%;
  color: #cccccc; /* Цвет текста контента */
}

/* Состояние для отображаемого контента */
.content.visible {
  max-height: 200px;
  padding: 20px;
}
